.enter {
  opacity: 0;
  transform: translateX(-50%);
}
.enterActive {
  opacity: 1;
  transform: translateX(0);
  transition: all 250ms ease;
}

.exit {
  opacity: 1;
  transform: translateX(0);
}

.exitActive {
  opacity: 0;
  transform: translateX(-50%);
  transition: all 250ms ease;
}
